<template>
      <div id="main">
        <span style="font-size:15px">当前同义词组标准名:</span>{{standardName}}
        <el-row>
          <el-col :span="24">
            <el-button type="primary" @click='sBatchUpdate'>批量更新标准术语同义词</el-button>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
           <el-table
              v-loading="sloading"
              element-loading-text="加载数据"
              :data="termDatas"
              style="width:100%"  
              empty-text="暂无数据"
              highlight-current-row
              @selection-change="shandleSelectionChange"
              >              
              <el-table-column
                type="selection"
                width="55">
              </el-table-column>
              <el-table-column label="术语ID" property="termId"  min-width="130"  header-align="center">
              </el-table-column>
              <el-table-column label="术语类型" property="termType" min-width="200" header-align="center">
              </el-table-column>
              <el-table-column label="原始名称" property="originName" min-width="120" header-align="center" > 
              </el-table-column> 
              <el-table-column label="术语名称" property="termName" min-width="120" header-align="center" >   
              </el-table-column>
              <el-table-column label="同义词" property="standardName" min-width="120" header-align="center">
              </el-table-column>
            </el-table>
            <el-pagination @size-change="sHandleSizeChange" @current-change="sHandleCurrentChange"
                      :current-page.sync="sPageIndex" :page-sizes="[10,20,30,50]" :page-size="sPageSize" layout="total, sizes, prev, pager, next, jumper" :total="sTotalLimit">
            </el-pagination>
          </el-col>
        </el-row>
      	<el-row>
          <el-col :span="24">
            <el-button type="primary" @click='aBatchUpdate'>批量更新原子术语同义词</el-button>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-table
	        v-loading="aloading"
	        element-loading-text="加载数据"
	        :data="atomicTermDatas"
	        style="width:100%"  
	        empty-text="暂无数据"
	        @selection-change="ahandleSelectionChange"
	        highlight-current-row
	        >     
	        <el-table-column
	                type="selection"
	                width="55">
	        </el-table-column>         
	        <el-table-column label="ID" property="id"  min-width="100"  header-align="center">
	        </el-table-column>
	        <el-table-column label="术语" property="term" min-width="160" header-align="center" >   
	        </el-table-column>
	        <el-table-column label="类型" property="type" min-width="160" header-align="center" >
	        </el-table-column>
	        <el-table-column
	          label="同义词" property="standardName" min-width="160" header-align="center">
	        </el-table-column>
	      </el-table>
	      <el-pagination @size-change="aHandleSizeChange" @current-change="aHandleCurrentChange"
	                :current-page.sync="aPageIndex" :page-sizes="[10,20,30,50]" :page-size="aPageSize" layout="total, sizes, prev, pager, next, jumper" :total="aTotalLimit">
	      </el-pagination>
          </el-col>
        </el-row>
        <el-dialog title="批量归组" :visible.sync='synonymVisible' size="tiny">
	        <el-form :model="concept" label-position="left" label-width="110px">
	          <el-form-item label="标准概念集">
	            <el-select v-model="concept.conceptValue" 
	              filterable allow-create
	              placeholder="请选择" style=" width:100%">
	              <el-option
	                v-for="item in concept.concepts"
	                :key="item.conceptId"
	                :label="item.standardName"
	                :value="item.conceptId">
	              </el-option>
	            </el-select>
	          </el-form-item>       
	          <el-form-item>
	            <el-button type="primary" @click="cConceptConfirm">确认</el-button>
	            <el-button @click='synonymVisible=false'>关闭</el-button>
	         </el-form-item>
	        </el-form>
      	</el-dialog>
      </div>
</template>
<script>
	import {getAllConcepts} from '@/api/concept'
	import {getTermsByConceptId,getAtomicTermsByConceptId,batchUpdateConceptIdOfTerm,batchUpdateConceptIdOfAtomicTerm} from '@/api/conceptGroup'
    export default{
      data(){
        return{
          standardName:null,
          sloading:false,
          aloading:false,
          conceptId:null,
          sPageIndex:1,
          sTotalLimit:0,
          sPageSize:10,
          aPageIndex:1,
          aTotalLimit:0,
          aPageSize:10,
          termDatas:[],
          atomicTermDatas:[],
          synonymVisible:false,
          concept:{
          	conceptValue:null,
          	concepts:[]
          },
          sMultipleSelection:[],
          aMultipleSelection:[]
        }
      },
      created(){
      	var self=this
      	self.getParams()
      	self.initStandardTable()
      	self.initAtomicTable()
      },
      methods:{
      	ahandleSelectionChange(val){
      		var self=this
            console.log(val)
            self.aMultipleSelection = val
      	},
      	shandleSelectionChange(val){
          var self=this
          console.log(val)
          self.sMultipleSelection = val
        },
		sBatchUpdate(){
		  var self=this
		  if(self.sMultipleSelection.length>0)
          {
            self.synonymVisible=true
            if(self.concept.concepts.length==0){
	            getAllConcepts()
	            .then((response)=>{
	              if(response.succ==true){
	                self.concept.concepts=response.data
	              }else{
	                self.$message.error(response.msg)
	              }
	            })
        	}
          }else{
            self.$message({
                type:'warning',
                message:'请先勾选术语记录！'
              });
           }
		},
		aBatchUpdate(){
			var self=this
			if(self.aMultipleSelection.length>0){
			  self.synonymVisible=true
			  if(self.concept.concepts.length==0){
		            getAllConcepts()
		            .then((response)=>{
		              if(response.succ==true){
		                self.concept.concepts=response.data
		              }else{
		                self.$message.error(response.msg)
		              }
		            })
        		}
	        }
	        else
	        {
              self.$message({
                type:'warning',
                message:'请先勾选原子术语记录！'
            });
          }
		},
      	cConceptConfirm(){
      		var self=this
      		if(self.sMultipleSelection.length>0){
			  	var idArr=[]
			  	Object.keys(self.sMultipleSelection).forEach((key)=>{
			  		idArr.push(self.sMultipleSelection[key].id)
			  	})
			  	console.log(idArr)
			  	batchUpdateConceptIdOfTerm(idArr,self.concept.conceptValue)
			  	.then((response)=>{
			  		if(response.succ==true){
			  			self.initStandardTable()	
			  			self.synonymVisible=false
			  		}
			  	})
			}
			if(self.aMultipleSelection.length>0){
				var idArr=[]
				Object.keys(self.aMultipleSelection).forEach((key)=>{
					idArr.push(self.aMultipleSelection[key].id)
				})
				batchUpdateConceptIdOfAtomicTerm(idArr,self.concept.conceptValue)
				.then((response)=>{
					if(response.succ==true){
						self.initAtomicTable()
						self.synonymVisible=false
					}
				})
			}
      	},
      	sHandleSizeChange(val){
          var self=this;
          self.sPageSize=val;
          self.initStandardTable();
        },
        sHandleCurrentChange(val){
          var self=this;
          self.sPageIndex=val;
          self.initStandardTable();
        },
        aHandleSizeChange(val){
          var self=this;
          self.aPageSize=val;
          self.initAtomicTable();
        },
        aHandleCurrentChange(val){
          var self=this;
          self.aPageIndex=val;
          self.initAtomicTable();
        },
        initAtomicTable(){
	    	var self=this
	    	self.aloading=true
	    	getAtomicTermsByConceptId(self.aPageIndex,self.aPageSize,self.conceptId)
      	   .then((response)=>{
      	   	if(response.succ==true){
      	   		self.atomicTermDatas=response.data.dataList
      	   		self.aTotalLimit=response.data.total
      	   	  }
      	   	  self.aloading=false
      	   })
        },
      	initStandardTable(){
      	   var self=this
      	   self.sloading=true
      	   getTermsByConceptId(self.sPageIndex,self.sPageSize,self.conceptId)
      	   .then((response)=>{
      	   	  if(response.succ==true){
      	   	  	self.termDatas=response.data.dataList
      	   	  	self.sTotalLimit=response.data.total
      	   	  }
      	   	  self.sloading=false
      	   })
      	},
        getParams(){
          var self=this
          // self.conceptId=unescape(self.$route.query.conceptId)
          // self.standardName=unescape(self.$route.query.standardName)
          var param=unescape(self.$route.query.param)
          self.conceptId=param.split('@')[0]
          self.standardName=param.split('@')[1]
        }
      },
      components:{
       
      }
    }
</script>


